<template>
    <div class="rank-tab" v-if="tabList && tabList.hasOwnProperty('list') && tabList.list.length>0">
        <el-button-group class="btn-group">
            <el-button class="btn-item"
                       v-for="(item,index) in tabList.list"
                       :type="tabList.type == item.type?'primary':'default'"
                       :key="index"
                       size="small"
                       @click="typeof item.func === 'function' ? item.func() : null">{{ item.text }}</el-button>
        </el-button-group>
    </div>
</template>

<script>
    export default {
        name:'rankTab',
        props: {
            tabList: {
                type: Object,
                default: ()=>{
                    return {};
                }
            }
        }
    }
</script>

<style lang="scss" scope>
    .rank-tab{
        display: inline-block;
        .btn-group{
            .btn-item{
                width: 100px;
            }
        }
    }
</style>