<template>
    <div class="order">
        <div class="list-top" v-if="tableData">
            <rank-tab :tab-list="theadName"></rank-tab>
            <div class="right-font">
                <el-input :placeholder="$L('输入订单编号')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="main-table" v-if="tableData && tableData.hasOwnProperty('list')" v-loading="loading">
            <el-table
                    v-loading="loading"
                    :data="tableData.list"
                    :empty-text="$L('暂无数据')"
                    class="table"
                    :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
                    @sort-change="sortChange">
                <el-table-column
                        prop="orderSn"
                        :label="$L('订单编号')"
                        align="left"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="prod"
                        :label="$L('商品')"
                        align="left"
                        width="180">
                    <div slot-scope="scope" class="details">
                        <div class="item" v-for="(i,k) in scope.row.productList" :key="k">
                            <img class="img" :src="i.img">
                            <div class="msg">
                                <p class="tit mainFontColor">{{ i.prodName }}</p>
                            </div>
                        </div>

                    </div>
                </el-table-column>
                <el-table-column
                        prop="username"
                        :label="$L('购买用户')"
                        align="center"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="relation"
                        :label="$L('分销关系')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="orderAmount"
                        :label="$L(' 实付金额')"
                        sortable
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="addTime"
                        :label="$L('下单时间')"
                        sortable
                        align="center">
                    <div slot-scope="scope">
                        <span v-if="scope.row.addTime">
                            {{ timeFunc(scope.row.addTime)[0] }}<br>
                            {{ timeFunc(scope.row.addTime)[1] }}
                        </span>
                        <span v-else>-</span>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="payTime"
                        :label="$L('支付时间')"
                        sortable
                        align="center">
                    <div slot-scope="scope">
                        <span v-if="scope.row.payTime">
                            {{ timeFunc(scope.row.payTime)[0] }}<br>
                            {{ timeFunc(scope.row.payTime)[1] }}
                        </span>
                        <span v-else>-</span>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="commissionPrice"
                        :label="$L('产生佣金')"
                        sortable
                        align="center">
                    <div slot-scope="scope">
                        {{ scope.row.commissionPrice}}
                    </div>
                </el-table-column>
                <el-table-column
                        prop="status"
                        :label="$L('结算状态')"
                        align="center"
                        width="100px">
                    <div slot-scope="scope" class="status">
                        <span :class="statusFunc(scope.row.pendingStatus).color">{{ statusFunc(scope.row.pendingStatus).text }}</span>
                    </div>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.allNum > pageSize">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        layout="prev, pager, next, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import  rankTab from "../../../components/common/rankTab.vue"
    export default {
        name: "order",
        components : {
            rankTab
        },
        data () {
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                inputValue:'',
                theadName:{
                    type:'all',
                    list:[
                        {
                            text: this.$L('全部订单'),
                            type:'all',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'all',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'all'}});
                                }
                            }
                        },
                        {
                            text:this.$L('已结算'),
                            type:'settlement',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'settlement',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'settlement'}});
                                }
                            }
                        },
                        {
                            text:this.$L('未结算'),
                            type:'unSettlement',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'unSettlement',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'unSettlement'}});
                                }
                            }
                        }
                    ],
                },
                tableData: {},
                pageSize:20,
                currentPage:1,
                userId:'',
                sortBy:'',
                name:''
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.currentPage = 1;
            this.getData(to);
            next();
        },
        created (){
            this.getData();
        },
        methods : {
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                };
                _self.theadName.type = router.params.hasOwnProperty('type') ? router.params.type : 'all';
                _self.userId = router.params.hasOwnProperty('id') ? router.params.id : '';
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    settlementStatus : _self.theadName.type,
                    keyword : _self.inputValue,
                    userId : _self.userId ,
                };
                _self.$api('distributionOrder', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回我的分销'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorList'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            statusFunc(value){
                let x;
                switch(value){
                    case 0:
                        x = {text:this.$L('未结算'),color:'font-red'};
                        break;
                    default:
                        x = {text:this.$L('已结算'),color:'font-green'};
                        break;
                }
                return x;
            },
            sortChange(column, prop, order){
                this.sortBy = column.prop;
                this.getData();
            },
			timeFunc(val){
				let arr = val.split(" ");
				return arr;
			}
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";
	@import "../../../assets/css/common/lib";
    .order{
        position: relative;
        padding: 0 20px 20px!important;
        .list-top{
            position: relative;
            padding-top: 10px;
            .right-font{
                position: absolute;
                right: 0;
                display: inline-block;
                .input-with-select{
                    width: 260px;
                    padding-left: 10px;
                    /deep/ .el-input-group__append{
                        font-size: 14px;
                        color: #333;
                        padding: 0 10px;
                        background: #fff;
                        border-left-color: #fff;
                    }
                }
            }
        }
        .main-table{
            padding: 20px;
            margin-top: 10px;
            background: #fff;
            /deep/ .el-table__body-wrapper{
                border-left: 1px solid #ebeef5;
                border-right: 1px solid #ebeef5;
            }
            /deep/ .el-table__header-wrapper .cell{
                font-weight: 800;
            }
            .table{
                width: 100%;
                .details{
                    .item{
                        margin-bottom: 10px;
                        .img{
                            width: 50px;
                            height: 50px;
                            background: #eee;
                            display: inline-block;
                            vertical-align: middle;
                        }
                        .msg{
                            display: inline-block;
                            vertical-align: middle;
                            padding-left: 5px;
                            width: calc(100% - 50px);
                            .tit{
                                text-overflow: ellipsis;
                                overflow: hidden;
                                white-space: nowrap;
                            }
                        }
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }

                }
                .status{
                    .font-red {
                        color: #ff0000;
                    }
                    .font-green {
                        color: #00bf00;
                    }
                }
            }
            .list-pages{
                text-align: center;
                padding: 20px 0;
                background: #fff;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>